import store from "../../store";
import {updateSiteModelChecklist} from "../../actions/cbChecklist_Actions";
import { toggleTab } from "../../actions/uiActions";

const OpenChecklist = ({item}) => {
    return <button onClick={()=>{store.dispatch(updateSiteModelChecklist({currentChecklist:Object.assign({},item)}));store.dispatch(toggleTab("checkTabs","execute"));}} className={"btn btn-info"}>Open</button>
}

export default OpenChecklist;